@keyframes fadeInUp {
    0% {
      opacity: 0;
      transform: translateY(30px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }

  .fade-in-up {
    opacity: 0;
    transform: translateY(30px);
    transition: opacity 0.9s ease-out, transform 0.9s ease-out;
  }

  .animate {
    opacity: 1;
    animation: fadeInUp 1s forwards;
  }

  .homePage-wrapper {
      padding: 0;
      margin-top: 0;
      background-color: white;
      max-width: 1440px;
      height: 590px;
      display: flex;
      justify-content: space-between;
  }

  .text-name {
      color: #ff7f80;
      font-size: 30px;
      font-weight: bolder;
      opacity: 0.9;
      transition: opacity 0.4s ease-in-out;
  }

  .text-name:hover {
      opacity: 1;
  }

  .homePage-text {
      padding-top: 40px;
      margin-left: 85.33px;
      width: 790.67px;
      height: 320px;
      color: black;
      text-align: left;
      opacity: 0;
      transform: translateY(20px);
      animation: fadeInUp 1.2s ease forwards;
  }

  .homePage-text-one {
      margin-top: 80px;
      width: 500px;
      height: 32px;
      line-height: 30px;
      margin-bottom: 20px;
      font-size: 30px;
      font-weight: 400;
      opacity: 0;
      transform: translateY(20px);
      animation: fadeInUp 1.2s ease forwards;
  }

  .homePage-text-one:hover {
      transform: scale(1.02);
  }

  .homePage-text-two {
      font-size: 19px;
      margin-top: 15px;
      margin-bottom: 30px;
      line-height: 30px;
      width: 550px;
      opacity: 0;
      transform: translateY(20px);
      animation: fadeInUp 1.2s ease forwards;
  }

  .homePage-text-two:hover {
      transform: scale(1.02);
  }

  .homePage-button {
      max-width: 150.67px;
      padding: 13px;
      height: 69.33px;
      color: white;
      font-family: Poppins;
      font-size: 18px;
      font-weight: 100%;
      text-align: center;
      background-color: #ff7f80;
      border-radius: 15px;
      border: 1px solid #ff7172;
      margin-right: 35px;
      transition: transform 0.3s;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  }

  .homePage-button:hover {
      cursor: pointer;
      background-color: #ff9899;
      font-weight: 500;
      transform: scale(1.05);
      box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.3);
  }

  .viewProject-button {
      max-width: 150.67px;
      padding: 10px;
      height: 69.33px;
      color: #ffa7a6;
      font-family: Poppins;
      font-size: 18px;
      font-weight: 100%;
      text-align: center;
      border-radius: 15px;
      border: 2px solid #ffa7a6;
      margin-right: 35px;
      transition: transform 0.3s;
      background-color: transparent;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  }

  .viewProject-button:hover {
      cursor: pointer;
      background-color: transparent;
      transform: scale(1.05);
      box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.3);
  }

  .home-logo {
      width: 350px;
      height: 360px;
      margin-right: 140.33px;
      margin-top: 98px;
      transition: transform 0.5s ease-in-out;
  }

  .home-logo:hover {
      transform: rotate(5deg);
  }

  .button-wrapper {
      display: flex;
      align-items: center;
      padding: 0;
      margin: 0;
      transition: transform 0.3s ease, opacity 0.3s ease;
  }

  .button-wrapper:hover {
      transform: translateY(-5px);
  }

  @media (max-width: 768px) {
    .homePage-wrapper {
        height: auto;
        padding: 10px;
        display: flex;
        justify-content: space-between; 
        align-items: center;
        flex-direction: row;
    }

    .home-logo {
        width: 180px; 
        height: auto;
        margin-right:40px;
        margin-bottom: 120px;
    }

    .homePage-text {
        width: 60%; 
        text-align: left;
        margin-left: 30px;
        margin-top: 0;
    }

    .homePage-text-one {
        margin-top: 0px;
        font-size: 22px !important;
    }

    .text-name {
        margin-top: 0;
        font-size: 22px !important;
    }

    .homePage-text-two {
        width: 100%;
        font-size: 14px !important;
        line-height: 24px;
        margin-top: 0;
    }

    .homePage-button, .viewProject-button {
        max-width: 140px;
        margin-right: 15px;
        height: 45px;
        font-size: 14px !important;
        margin-top: 10px;
    }

    .button-wrapper {
        display: flex;
        justify-content: flex-start;
        flex-wrap: nowrap;
    }
}

@media (max-width: 390px) {
    .home-logo {
        display: none;
    }

    .homePage-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 10px;
        margin: 0 auto;
        width: 100%;
        height: auto; 
        box-sizing: border-box;
    }

    .homePage-text {
        width: 90%; 
        text-align: left;
        padding: 0;
        margin: 0;
    }

    .homePage-text-one {
        margin-top: 60px; 
        font-size: 15px !important; 
        line-height: 20px;
        margin-bottom: 10px; 
        margin-left: 35px;
    }

    .text-name {
        font-size: 16px !important; 
        text-align: center;
    }

    .homePage-text-two {
        width: 90%; 
        font-size: 13px !important; 
        line-height: 20px; 
        margin: 0 auto; 
        margin-bottom: 20px;
        text-align: left; 
    }

    .homePage-button, .viewProject-button {
        width: 130px; 
        padding: 8px;
        height: 40px;
        font-size: 12px !important;
        margin-top: 10px;
    }

    .button-wrapper {
        display: flex;
        justify-content: center;
        gap: 10px;
        width: 100%;
        margin: 0 auto; 
        padding: 0;
    }
}
